<template>
  <el-container>
    <div class="main">
      <el-header>
        <HeaderTitle />
      </el-header>
      <el-main>

          <div id="tabs">
            <ul class='tabUl'>
              <li
                v-for="(tab,index) in tabs"
                @click="toggle(index,tab.type)"
                :class="{active:active==index}"
                :key="index"
              >{{tab.name}}</li>
            </ul>
          </div>
          <div class="searchDiv">
            <div>
              <span>设备名称</span>
              <el-input
                v-model='searchName'
                placeholder="请输入设备名称"
                size='mini'>
              </el-input>
            </div>
              <el-button
                @click="search"
                size='mini'
                type="primary"
              >{{BTN_TEXT.SEARCH}}</el-button>
              <el-button
                @click="reset"
                size='mini'
              >{{BTN_TEXT.RESET}}</el-button>
          </div>

           <VXETable id="table"
           :height="$options.filters.getTableHeight(240)"
            show-overflow
            highlight-hover-row
            :tableName="''"
            :show-header="true"
            :tableColumns="tableColumns"
            :tableDatas="tableDatas"
            :tablePage="tablePages"
            :tableLoading="tableLoading"
             @onTablePageChange="onTablePageChanges"
          ></VXETable>

      </el-main>
    </div>
    <el-dialog
      title="配置上限"
      class='dialog'
      width="500px"
      :visible.sync="showUpdate">
      <el-form :model="form" label-width="150px" :rules="rules" ref="rawForm">
        <!-- <el-form-item label="名称" prop='deviceName' required="">
          <el-input v-model="form.deviceName"
            :readonly="true"></el-input>
        </el-form-item> -->
        <el-form-item label="每小时用量上限"  prop="upper_limit">
          <el-input v-model="form.upper_limit"></el-input>
        </el-form-item>
      </el-form>
       <span slot="footer" class="dialog-footer">
         <el-button type="primary" @click='changeLimit'>{{BTN_TEXT.SAVE}}</el-button>
        <el-button @click="showUpdate = false">{{BTN_TEXT.CANCEL}}</el-button>
       </span>
    </el-dialog>
  </el-container>
</template>

<script>
import js from '@/assets/js/energy/EnergyLimitSet/index.js';
export default js;
</script>

<style lang="less" scoped>
@import '../../../assets/less/energy/EnergyLimitSet/index.less';
</style>
