import { showErrorNotify } from '@/utils/notify';
import { GAS_NAME } from '@/utils/ajaxConfigure';
import { showWarningMessage, showSuccessMessage } from '@/utils/message';

import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import VXETable from '@/components/common/Table.vue';
// import axios from 'axios';
import { energyQueryWarningLimitsAction, energySetWarningLimitsAction } from '@/api/energyConfig';
import {
  STATUS_CODE,
  TITLE,
  BTN_TEXT,
  EQUIPMENT_STATUS_GANTT
} from '@/utils/constantsFile';
export default {
  name: 'EnergyLimitSet',
  components: {
    HeaderTitle,
    VXETable
  },
  data() {
    return {
      GAS_NAME: GAS_NAME,
      EQUIPMENT_STATUS_GANTT: EQUIPMENT_STATUS_GANTT,
      BTN_TEXT: BTN_TEXT,
      maxHeight: document.body.clientHeight - 300,
      tableLoading: false,
      active: 0,
      type: 0,
      tabs: [
        {
          type: 0,
          name: '水表',
          typeName: 'WATER'
        },
        {
          type: 1,
          name: '电表',
          typeName: 'ELECTRICITY'
        },
        {
          type: 2,
          name: GAS_NAME + '表',
          typeName: 'GAS'
        }
      ],
      unit: ['m³', 'KW·H', 'm³'],
      searchName: '',
      tableDatas: [],
      tablePages: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      },
      showUpdate: false,
      form: {
        sbCode: '',
        isOpen: 0,
        upper_limit: 0
      },
      rules: {
        upper_limit: [
          {
            required: true,
            message: '请输入阈值',
            trigger: 'blur'
          }
        ]
      }
    };
  },
  computed: {
    tableColumns: function() {
      return [
        {
          field: 'sbName',
          title: '名称',
          align: 'center'

        },
        {
          field: 'upper_limit',
          title: `每小时用量上限（${this.unit[this.type]}）`,
          align: 'center',
          formatter: ({ cellValue, row, column }) => {
            // return cellValue > 99999999;
            if (cellValue > 99999999) {
              return '>99999999';
            } else {
              return cellValue;
            }
          }
        },
        // {
        //   field: 'isOpen',
        //   title: '是否启用告警配置',
        //   align: 'center',
        //   formatter: ({ cellValue, row, column }) => {
        //     if (cellValue === 1) {
        //       return '是';
        //     } else {
        //       return '否';
        //     }
        //   }
        // },
        {
          title: BTN_TEXT.OPERATION,
          align: 'center',
          slots: {
            default: ({ row }, h) => {
              var res = [];
              // if (row.isOpen === 1) {
              //   res = [<el-dropdown
              //     placement="bottom"
              //     size="small"
              //     onCommand={(command) => this.handleCommand(command, row)}
              //   >
              //     <span class="operation-column" >
              //       {BTN_TEXT.OPERATION}
              //     </span>
              //     <el-dropdown-menu slot="dropdown">
              //       <el-dropdown-item class="operation-column" command="state">取消告警上限</el-dropdown-item>
              //       <el-dropdown-item class="operation-column" command="change">配置告警上限</el-dropdown-item>
              //     </el-dropdown-menu>
              //   </el-dropdown>];
              // } else {
              res = [<el-dropdown
                placement="bottom"
                size="small"
                onCommand={(command) => this.handleCommand(command, row)}
              >
                <span class="operation-column" >
                  {BTN_TEXT.OPERATION}
                </span>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item class="operation-column" command="change">配置告警上限</el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>];
              // }

              return res;
            }
          }
        }
      ];
    }
  },
  methods: {
    toggle(i, type) {
      this.active = i;
      this.type = type;
      this.onTablePageChanges(1, 10);
    },
    handleCommand(command, row) {
      if (command === 'state') {
        this.changeState(row);
      } else if (command === 'change') {
        this.form = {
          ...row
        };
        if (this.form.upper_limit > 99999999) {
          this.form.upper_limit = 100000000;
        }
        this.showUpdate = true;
      }
    },
    async changeState(row) {
      try {
        var upperLimit = 0;
        if (row.upper_limit > 99999999) {
          upperLimit = 100000000;
        } else {
          upperLimit = row.upper_limit;
        }

        const res = await energySetWarningLimitsAction({}, [{
          sbCode: row.sbCode,
          isOpen: (row.isOpen + 1) % 2,
          upper_limit: upperLimit
        }]);
        if (res.code) {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            // // 调用列表查询接口
            // this.dayEnergyConsumption = res.rows.dayEnergyConsumption;
            // this.yesterdayEnergyConsumption = res.rows.yesterdayEnergyConsumption;

            showSuccessMessage(res.message);
            this.findList();
            // 提示异常信息
          } else {
            // this.tableDatas = [];
            // this.tablePages.total = 0;
            // showWarningMessage(data.message);
            // 提示异常信息
            showWarningMessage(res.message);
          }
        }
      } catch (error) {
        // this.tableDatas = [];
        // this.tablePages.total = 0;
        showErrorNotify(TITLE.NOTIFY, error);
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        // this.tableLoading = false;
      }
    },
    async changeLimit() {
      try {
        const res = await energySetWarningLimitsAction({}, [this.form]);
        if (res.code) {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            // // 调用列表查询接口
            // this.dayEnergyConsumption = res.rows.dayEnergyConsumption;
            // this.yesterdayEnergyConsumption = res.rows.yesterdayEnergyConsumption;

            showSuccessMessage(res.message);
            this.showUpdate = false;
            this.findList();
            // 提示异常信息
          } else {
            // this.tableDatas = [];
            // this.tablePages.total = 0;
            // showWarningMessage(data.message);
            // 提示异常信息
            showWarningMessage(res.message);
          }
        }
      } catch (error) {
        // this.tableDatas = [];
        // this.tablePages.total = 0;
        showErrorNotify(TITLE.NOTIFY, error);
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        // this.tableLoading = false;
      }
      // try {
      //   await this.$refs.rawForm.validate();
      //   const { data } = await axios.post(
      //     'http://localhost:8081/idevcloudwebsub39/energy/setWarningLimits.action',
      //     [
      //       {
      //         sbCode: this.form.nypzDeviceCode,
      //         isOpen: this.form.nypzState,
      //         lower_limit: this.form.nypzLowerLimit,
      //         upper_limit: this.form.nypzUpperLimit
      //       }
      //     ]

      //   );
      //   if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
      //     showSuccessMessage(data.message);
      //     this.showUpdate = false;
      //     this.findList();
      //   } else {
      //     showWarningMessage(data.message);
      //   }
      // } catch (error) {
      //   showErrorNotify(TITLE.NOTIFY, error);
      // }
    },
    async findList() {
      try {
        this.tableLoading = true;
        const res = await energyQueryWarningLimitsAction({
          pageSize: this.tablePages.pageSize,
          pageNum: this.tablePages.currentPage,
          energyEnum: this.tabs[this.type].typeName,
          deviceName: this.searchName
        });
        if (res.code) {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            // // 调用列表查询接口
            // this.dayEnergyConsumption = res.rows.dayEnergyConsumption;
            // this.yesterdayEnergyConsumption = res.rows.yesterdayEnergyConsumption;

            this.tableDatas = res.rows;
            this.tablePages.total = res.total;
            // 提示异常信息
          } else {
            this.tableDatas = [];
            this.tablePages.total = 0;
            // showWarningMessage(data.message);
            // 提示异常信息
            showWarningMessage(res.message);
          }
        }
      } catch (error) {
        this.tableDatas = [];
        this.tablePages.total = 0;
        showErrorNotify(TITLE.NOTIFY, error);
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.tableLoading = false;
      }
    },
    onTablePageChanges(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePages.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePages.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    },
    search() {
      this.onTablePageChanges(1, this.tablePages.pageSize);
    },
    reset() {
      this.searchName = '';
      this.onTablePageChanges(1, 10);
    }
  },
  mounted() {
    this.findList();
  }
};
